import React, { useState } from "react";
import {
  BrowserRouter,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Home from './pages/Home';
import About from './pages/About';
import 'bootstrap/dist/css/bootstrap.css';
import './css/site.css'

export default function App(props) {
  const [collapseNavMenu, setCollapseNavMenu] = useState(false);
  return <div className="app">
    <BrowserRouter basename={process.env.PUBLIC_URL + '/'}>
      <div className="sidebar">
        <div className="top-row pl-4 navbar navbar-dark" >
          <button className="navbar-toggler" onClick={() => setCollapseNavMenu(!collapseNavMenu)}>
            <span className="navbar-toggler-icon"></span>Меню
          </button>
          <a className="navbar-brand" href="https://ru.reactjs.org/" target="_blank" rel="noopener noreferrer">{props.brand}</a>
        </div>
        <div className={collapseNavMenu ? "collapse" : null}>
          <nav >
            <ul className="nav flex-column">
              <li className="nav-item px-3">
                <Link to="/" className="nav-link">
                  <span className="oi oi-home" aria-hidden="true"></span>Home
                </Link>
              </li>
              <li className="nav-item px-3">
                <Link to="/about" className="nav-link">
                  <span className="oi oi-sun" aria-hidden="true"></span>About
                </Link>
              </li>
            </ul>
          </nav>
        </div>
      </div>
      <div className="main">
        <div className="top-row px-4">
          <a href="https://ru.reactjs.org/" target="_blank" className="ml-md-auto" rel="noopener noreferrer">About</a>
        </div>

        <div className="content px-4">
          <Switch >
            <Route path="/about">
              <About />
            </Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </div>
      </div>
    </BrowserRouter>
  </div>;
}
